<template>
  <div class="project-card">
    <i class="el-icon-edit" @click.stop="editProject"></i>
    <i class="el-icon-delete" @click.stop="deleteProject"></i>
    <el-image fit="fill" :src="project.overviewPic"> </el-image>
    <p :title="project.name">{{ project.name }}</p>
  </div>
</template>

<script>
export default {
  name: "ProjectCard",
  props: ["project"],
  methods: {
    deleteProject() {
      this.$emit("delete", this.project);
    },
    editProject() {
      this.$emit("edit", this.project);
    },
  },
};
</script>

