        return res;
      },
      (err) => {
        return null;
      }
    );
    if (koolsmartCfg) {
      const cfg = koolsmartCfg.action[0].variables;
      this.$store.commit("setProjects", cfg.projects);
      // devices可用于自定义实体样式：图标
      this.$store.commit("setDevices", cfg.devices);

      // xiaolou
      this.$store.commit("projectStore/setProjects", cfg.projects);
      this.projects = cfg.projects;
    } else {
      createKoolsmartCfg().then((res) => {
        this.$message.success("create koolsmart config in home assistant.");
      });
      this.$store.commit("setProjects", []);
      this.$store.commit("setDevices", []);
      // xiaolou
      this.$store.commit("projectStore/setProjects", []);
    }
