      getDesignList(
        { start: this.designStart, num: this.designNum },
        this.$store.state.koolsmartStore.accountType
      ).then(
        (res) => {
          if (res.result) {
            this.designList = this.designList.concat(res.result);
            // this.designList = [...this.designList, ...res.result];
            this.designStart += res.result.length;
          }
          this.hasMore = res.hasMore;
        },
        (err) => {
          this.$message.error(err);
        }
      );
    },
    validateCreateProjectForm() {
      if (!this.newProjectForm.name) {
        this.$message.warning("请填写方案名称");
        return false;
      }
      if (!this.selectedDesign) {
        this.$message.warning("请关联酷家乐设计方案");
        return false;
      }
      return true;
    },
    async handleProjectDialogConfirmBtnClick() {
      if (!this.validateCreateProjectForm()) {
        return;
      }
      // 获取当前方案下的渲染图
      let renderPicInfos = await getDesignRenderpics(
        { obsDesignId: this.selectedDesign.obsDesignId },
        this.$store.state.koolsmartStore.accountType
      ).then(
        (res) => {
          return res.renderPicInfos;
        },
        (err) => {
          this.$message.error(err);
          return null;
        }
      );
      if (!renderPicInfos || renderPicInfos.length === 0) {
        this.$message.warning("该方案下没有可用渲染图!");
        return;
      }
      // 新建project结构体
      // 一个project相当于一个dashboard
      // url需要唯一
      let uid = generateUniqueId();
      let newProject = {
        ...this.newProjectForm,
        obsDesignId: this.selectedDesign.obsDesignId,
        // renderPics放到后面实时获取
        // renderPics: renderPics,
        overviewPic: this.selectedDesign.overviewPic,
        id: uid,
        url: `koolsmart-${uid}`,
      };
      this.$store.commit("projectStore/setSelectedProject", newProject);
      this.$store.commit("projectStore/addProject", newProject);
      this.$message.success("new project success!");
      this.$router.push("/project/scene");
    },
    async handleNewProjectBtnClick() {
      this.designList = await getDesignList(
        { start: 0, num: this.designNum },
        this.$store.state.koolsmartStore.accountType
      ).then(
        (res) => {
          this.designStart = this.designNum;
          return res.result;
        },
        (err) => {
          this.$message.error(err);
          return [];
        }
      );
      this.addProjectDialogFormVisible = true;
    },
