    handleProjectDeleteBtnClick(project) {
      console.log(project);
      this.$confirm("你确定要删除这个项目吗？", "Prompt", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(
        (res) => {
          let projects = this.$store.state.projectStore.projects;
          projects = projects.filter((item) => item.id !== project.id);
          this.$store.commit("projectStore/setProjects", projects);
          updateKoolsmartCfg(projects).then(
            (res) => {
              this.$message.success("项目删除成功");
              this.projects = projects;
            },
            (err) => {
              console.log(err);
            }
          );
        },
        (err) => []
      );
    },
    handleProjectEditBtnClick(project) {
