    },
    handleDuplicate(name) {
      while (name in this.projects) {
        if (name[name.length - 2] == "-") {
          name = name.slice(0, name.length - 1) + this.n;
        } else {
          name = name + "-" + this.n;
        }
        this.n += 1;
      }
      this.n = 1;
      return name;
    },
    projectDialogConfirm: throttle(function () {
      if (this.radio == "") {
        this.$message.warning("Please select a floor plan!");
        return;
      }
      let temp = this.radio.split(",");
      let id = temp[0];
      let name = temp[1];
      name = this.handleDuplicate(name);
      this.$store.commit("setProjectName", name);
      this.$store.commit("setPlanId", id);
      this.projects[name] = {};
      this.projects[name]["id"] = id;
      this.projects[name]["name"] = name;
      this.projects[name]["scenarios"] = {};
      this.projects[name]["default"] = "";
      this.projects[name]["scenarioList"] = [];
      const get_render = async () => {
        let res = await getDesignRenderpics(
          { obsDesignId: id },
          this.$store.state.koolsmartStore.accountType
        );
        if (!res.data) {
          this.$notify.error({
            title: "error",
            message: "Server response data is empty.",
          });
          return;
        }
        // Set default image.
        if (res.data.renderPics.length == 0) {
          this.projects[name].default = this.visitor.renderPics[0];
        } else {
          this.projects[name].default = res.data.renderPics[0];
        }
        this.addProjectDialogFormVisible = false;
        this.projects = JSON.parse(JSON.stringify(this.projects));
        this.$store.commit("setProjects", JSON.stringify(this.projects));
        this.radio = "";
        this.$router.push("/project/scene");
      };
      if (this.$store.state.isVisitor) {
        // visitor mode
        this.addProjectDialogFormVisible = false;
        this.projects[name].default = this.visitor.renderPics[0];
        this.projects = JSON.parse(JSON.stringify(this.projects));
        this.$store.commit("setProjects", JSON.stringify(this.projects));
        this.radio = "";
        this.$router.push("/project/scene");
      } else {
        get_render();
      }
    }),
